




























import Vue from "vue";
import store from "@/store";
export default Vue.extend({
  name: "NavBar",
  store,
  props: { appTitle: String },
  methods: {
    changeMode(): void {
      this.$store.commit("changeMode");
    },
    addCounter(): void {
      this.$store.commit("addCounter", {
        name: "new Counter",
        maxCount: 10,
        currentCount: 0,
        selected: false,
        resetOn: "ShortRest",
      });
      this.changeMode();
    },
    removeCounters(): void {
      this.$store.commit("removeSelectedCounters");
    },
  },
});
